/* Reset css */
:root {
  font-size: xx-small; }

* {
  margin: 0 auto;
  padding: 0 auto;
  color: #333;
  outline-color: transparent !important;
  font-family: 'montserrat',Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 500; }

*:focus {
  outline-color: transparent !important;
  box-shadow: none !important;
  border: 0 !important;
  outline-offset: 0px !important;
  z-index: initial !important; }

*:before, *:after {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

li {
  line-height: inherit;
  list-style: none; }

a {
  text-decoration: none;
  color: inherit; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  margin: 0;
  padding: 0;
  line-height: initial; }

p {
  padding: 0;
  margin: 0; }

a:focus, a:active,
button:focus,
button:active,
.btn:focus,
.btn:active,
.button:focus,
.button:active {
  outline: none;
  text-decoration: none;
  color: inherit; }

button,
input[type="button"] {
  cursor: pointer; }

a:hover {
  text-decoration: none;
  color: inherit; }

select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
  display: inline-block;
  height: inherit;
  padding: inherit;
  margin-bottom: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  vertical-align: middle;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0; }

input[type="checkbox"],
input[type="radio"],
select[type="checkbox"],
select[type="radio"],
textarea[type="checkbox"],
textarea[type="radio"] {
  padding: 0; }

a:focus {
  color: #333; }

a:hover {
  color: #666; }

body {
  width: 100%;
  font-size: small;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif; }

ul {
  margin: 0;
  padding: 0; }

p {
  color: #333;
  font-size: 14px;
  margin: 0;
  padding: 0; }

a {
  color: #333;
  text-decoration: none; }

a:hover {
  text-decoration: none; }

h3 {
  margin: 0;
  padding: 0; }

.containerFull {
  width: 100%;
  display: block;
  padding: 0;
  margin: 0; }

.containerCenter {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  display: table; }

.nav .open > a,
.nav .open > a:focus,
.nav .open > a:hover {
  background: transparent; }

.header {
  display: block;
  background: #2685aa;
  margin: 0 0 16px 0; }

#titlePage {
  width: 100%;
  display: block;
  font-size: 28px;
  font-weight: 600;
  color: #fff;
  text-align: center;
  padding: 16px 0; }

body {
  background: #eeeeee; }

.footer {
  background: #999; }
  .footer .containerCenter {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center; }
  .footer__ass {
    color: #fff; }

.cards {
  width: 100%;
  height: auto;
  display: inline-flex;
  justify-content: center;
  flex-wrap: wrap; }
  .cards__card {
    width: calc(100% - 32px);
    background: #fff;
    margin: 16px;
    padding-bottom: 16px; }
    .cards__card img {
      width: 100%;
      height: auto; }
    .cards__card--name {
      width: calc(100% - 16px);
      display: block;
      font-size: 16px;
      font-weight: 600;
      margin: 16px 8px; }
    .cards__card--species, .cards__card--status, .cards__card--gender {
      width: calc(100% - 16px);
      display: block;
      font-size: 14px;
      font-weight: 600;
      margin: 8px 8px; }
      .cards__card--species strong, .cards__card--status strong, .cards__card--gender strong {
        font-weight: 400; }

#moreCards {
  width: calc(100% - 32px);
  text-align: center;
  font-weight: 600;
  color: #fff;
  background: #2685aa;
  padding: 16px 0;
  margin: 16px;
  border: 0;
  cursor: pointer; }

@media only screen and (min-width: 375px) {
  .cards__card {
    width: calc(50% - 24px);
    margin: 8px; }
    .cards__card:nth-child(1n) {
      margin-left: 8px; }
    .cards__card:nth-child(2n) {
      margin-right: 8px; } }

@media only screen and (min-width: 375px) {
  .cards__card {
    width: calc(50% - 24px);
    margin: 8px; }
    .cards__card:nth-child(1n) {
      margin-left: 8px; }
    .cards__card:nth-child(2n) {
      margin-right: 8px; } }

@media only screen and (min-width: 768px) {
  .cards__card {
    width: calc(25% - 24px);
    margin: 8px;
    transition: box-shadow linear .5s; }
    .cards__card:hover {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
      transition: box-shadow linear .5s; }
  #moreCards {
    width: 500px;
    display: table;
    margin: 16px auto;
    transition: background linear .5s; }
    #moreCards:hover {
      background: #16536b;
      transition: background linear .5s; } }

@media only screen and (min-width: 1024px) {
  .cards__card {
    width: calc(20% - 24px);
    margin: 8px; } }
